<template>
    <div>
        <el-form ref="form" :model="form" label-width="110px" :rules="rules">
            <el-collapse v-model="collapseActive" @change="">
                <el-collapse-item title="　基本信息" name="1">

                    <el-form-item label="公司名称" prop="insurance_name">
                        <el-input v-model="form.insurance_name" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="付款流程">
                        <el-radio-group v-model="form.receipt_flow" :disabled="isDisabled()">
                            <el-radio label="1">先付款</el-radio>
                            <el-radio label="2">后付款</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="收款账号">
                        <el-input v-model="form.receipt_account" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="收款银行">
                        <el-input v-model="form.receipt_bank" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="联系人">
                        <el-input v-model="form.contact" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="移动电话">
                        <el-input v-model="form.mobile" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="固定电话">
                        <el-input v-model="form.fixed_telephone" :disabled="isDisabled()"></el-input>
                    </el-form-item>
                    <el-form-item label="公司地址">
                        <el-input v-model="form.company_address" :disabled="isDisabled()"></el-input>
                    </el-form-item>

                </el-collapse-item>

                <el-collapse-item title="　保险信息" name="2">
                    
                    <el-table :data="form.product_info" border class="table" ref="">
                        <el-table-column prop="insurance_name" label="保险名称" width="150">
                        </el-table-column>
                        <el-table-column prop="dict_name" label="保险种类" width="120">
                        </el-table-column>
                         <el-table-column prop="insurance_amount" label="保险价格">
                        </el-table-column>
                        <el-table-column prop="insurance_price" label="保险金额">
                        </el-table-column>
                        <el-table-column prop="status" label="是否启用">
                        </el-table-column>
                        <el-table-column prop="create_user" label="创建人">
                        </el-table-column>
                        <el-table-column prop="create_time" label="创建时间">
                        </el-table-column>
                    </el-table>

                </el-collapse-item>

            </el-collapse>
            <el-form-item class="form-submit">
                <el-button type="primary" @click="handleSubmit" v-if="pageType != 'view'">保存</el-button>
                <el-button @click="$router.back()">返回</el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>

<script>
    import * as API from '@/api/resourceManagement'
    export default {
        data: function(){
            return {
                collapseActive: ['1'],
                pageType:"",
                form: {
                    company_address: "",
                    insurance_name: "",
                    contact: "",
                    create_time: "",
                    create_user: "",
                    fixed_telephone: "",
                    mobile: "",
                    receipt_account: "",
                    receipt_bank: "",
                    receipt_flow: '1',
                    product_info: [{
                        create_time: "",
                        create_user: "",
                        dict_type_name: "",
                        insurance_amount: "",
                        insurance_name: "",
                        insurance_price: "",
                        status: ""
                    }]
                },
                rules: {
                    insurance_name: [
                        { required: true, message: '请输入公司名称', trigger: 'blur' }
                    ]
                }
            }
        },
        mounted(){
            this.checkPath()
        },
        methods: {
            // 是否禁止输入
            isDisabled () {
                let d = this.pageType == 'view' ? true : false;
                return d;
            },
            checkPath() {
                let _path = this.$route.path,
                    _query = this.$route.query;
                if(_path.indexOf('add') > -1) {
                    this.pageType = 'add';
                }else if(_path.indexOf('edit') > -1) {
                    this.getFormData();
                    this.pageType = 'edit';
                }else if(_path.indexOf('view') > -1) {
                    this.pageType = 'view';
                    this.getFormData();
                }
            },
            getFormData(){
                API.apiInsuranceSupplierView({id: this.$route.query.id}).then(res => {
                    Object.assign(this.form,res.data)
                })
            },
            handleSubmit() {
                this.$refs.form.validate((valid)=>{
                    if(valid){
                        if(this.pageType == 'add'){
                            API.apiInsuranceSupplierAdd(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }else{
                            API.apiInsuranceSupplierEdit(this.form).then(res => {
                                this.$message.success('提交成功！');
                                this.$router.back()
                            })
                        }
                    }else{
                        return false
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>
    
</style>